<template>
    <div id="team" class="d-flex flex-column justify-start align-center mb-15">
        <v-container>
            <div
                class="my-10 mb-15 d-flex flex-column justify-center align-center"
            >
                <p class="team-title scroll-out-item-top">our team</p>
                <v-img
                    :src="require('../../assets/chat-app/line.png')"
                    class="my-2 mb-15"
                    contain
                    width="100"
                />
            </div>
            <v-row>
                <v-col cols="12" md="4" v-for="i in 3" :key="i" class="scroll-out-item-top-sp">
                    <v-hover v-slot="{ hover }" open-delay="200">
                        <v-card
                            class="mx-auto"
                            color="grey lighten-4"
                            max-width="600"
                        >
                            <v-img
                                :src="
                                    require('../../assets/chat-app/team-' +
                                        i +
                                        '.jpg')
                                "
                            >
                                <v-expand-transition>
                                    <div
                                        v-if="hover"
                                        class="d-flex transition-fast-in-fast-out v-card--reveal2 display-3"
                                    >
                                        <v-btn class="mx-4" icon>
                                            <v-icon size="20px" color="white"
                                                >mdi-plus</v-icon
                                            >
                                        </v-btn>
                                    </div>
                                </v-expand-transition>
                                <v-expand-transition>
                                    <div
                                        v-if="hover"
                                        class="d-flex transition-fast-in-fast-out v-card--reveal display-3"
                                    >
                                        <v-btn
                                            v-for="icon in icons"
                                            :key="icon"
                                            class="mx-2"
                                            icon
                                        >
                                            <v-icon size="20px" color="white">{{
                                                icon
                                            }}</v-icon>
                                        </v-btn>
                                    </div>
                                </v-expand-transition>
                            </v-img>
                        </v-card>
                    </v-hover>
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
import ScrollOut from "scroll-out";
import { mapGetters } from "vuex";
export default {
    name: "Team",

    mounted() {
        this.so = ScrollOut({
            scope: this.$el,
            once: this.getScrollOutOnce,
            threshold: 0.5,
            targets:
                ".scroll-out-item-top, .scroll-out-item-top-sp",
        });
    },

    destroyed() {
        this.so.teardown();
    },

    computed: {
        ...mapGetters({
            getScrollOutOnce: "getScrollOutOnce",
        }),
    },

    data: () => ({
        icons: [
            "mdi-facebook",
            "mdi-twitter",
            "mdi-linkedin",
            "mdi-instagram",
            "mdi-google-plus",
            "mdi-rss",
        ],
    }),
};
</script>

<style scoped>
#team {
    font-family: "Poppins", sans-serif;
    min-height: 100vh;
}
.team-title {
    font-size: 2.2em;
    font-weight: 600;
    color: #6e51e6;
    text-transform: capitalize;
}

.v-card--reveal,
.v-card--reveal2 {
    align-items: center;
    justify-content: center;
    opacity: 1;
    position: absolute;
    background-image: linear-gradient(to bottom right, #6c52e7, #9248dd);
}
.v-card--reveal {
    bottom: 0;
    width: 100%;
    height: 10%;
}
.v-card--reveal2 {
    top: 0;
    right: 0;
    width: 10%;
    height: 10%;
    border-bottom-left-radius: 10px;
}
</style>
